import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/payment/paymentTerm/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, StatusTag } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

// const Panel = Collapse.Panel;
// const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class PaymentTerm extends Component {
  state = {
    filter: {
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getPaymentTerms } = this.props;
    getPaymentTerms();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.paymentTerm.error) {
      notification("error", this.props.paymentTerm.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.paymentTerm.success) {
      notification("success", this.props.paymentTerm.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getPaymentTerms } = this.props;
    this.setState({
      filter: {
        id_account: [],
        pagination: null
      }
    });
    getPaymentTerms(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getPaymentTerms } = this.props;
    this.setState({
      filter: {
        pagination: null
      }
    });
    this.setState({ search: null });
    getPaymentTerms();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getPaymentTerms } = this.props;
    //const { current_page } = this.props.paymentTerm;
    const { filter } = this.state;
    let string = "";

    if (filter.id_account) {
      string += "id_account=" + filter.id_account;
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getPaymentTerms(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPaymentTerms } = this.props;
    this.setState({
      filter: {
        id_account: [],
        transaction_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getPaymentTerms();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activatePaymentTerm(id);
    } else {
      this.props.deactivatePaymentTerm(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getPaymentTerms } = this.props;
    const { search } = this.props.paymentTerm;
    const { filter } = this.props.paymentTerm;
    getPaymentTerms(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_payment_term",
    rowKey: "id_payment_term",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="paymentTerms.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="paymentTerms.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    // console.log(this.state.filter);
    const // Definisi kolom2 serta field2 API yang dipakai tabel
      columns = [
        {
          title: getLanguage({ id: "code" }),
          dataIndex: "payment_term_code",
          rowKey: "payment_term_code",
          width: "20%",
          render: text => <span>{text}</span>
        },
        {
          title: getLanguage({ id: "name" }),
          dataIndex: "description",
          rowKey: "description",
          width: "40%",
          render: display => {
            return display.length > 50
              ? display.substring(0, 50) + "..."
              : display;
          }
        },
        // {
        //   title: "Discount",
        //   dataIndex: "discount",
        //   rowKey: "discount",
        //   width: "5%",
        //   align: "center",
        //   render: text => <span>{parseFloat(text*100).toFixed(2)}%</span>
        // },
        {
          title: getLanguage({ id: "paymentTerms.add.minimumPaymentDay" }),
          dataIndex: "minimum_payment_day",
          rowKey: "minimum_payment_day",
          width: "10%",
          align: "center",
          render: text => <span>{text}</span>
        },
        {
          title: getLanguage({ id: "paymentTerms.add.netPaymentDay" }),
          dataIndex: "net_payment_day",
          rowKey: "net_payment_day",
          width: "10%",
          align: "center",
          render: text => <span>{text}</span>
        },
        {
          title: getLanguage({ id: "cash" }),
          dataIndex: "is_cash",
          rowKey: "is_cash",
          width: "5%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "primary";
              display = getLanguage({ id: "yes" });
            } else {
              className = "nonactive";
              display = getLanguage({ id: "no" });
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        },
        {
          title: getLanguage({ id: "isDefault" }),
          dataIndex: "is_default",
          rowKey: "is_default",
          width: "5%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "primary";
              display = <IntlMessages id="yes" />;
            } else {
              className = "nonactive";
              display = <IntlMessages id="no" />;
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        },
        {
          title: getLanguage({ id: "status" }),
          dataIndex: "is_active",
          rowKey: "is_active",
          width: "10%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "active";
              display = getLanguage({ id: "active" });
            } else {
              className = "nonactive";
              display = getLanguage({ id: "inactive" });
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        }
      ];

    // Data setup
    const { payment_terms } = this.props.paymentTerm;
    //console.log(this.props.paymentTerm); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Payment Term") && columns.length === 7) {
      columns.push(this.listAction);
    }

    Object.keys(payment_terms).map((paymentTerm, index) => {
      return dataSource.push({
        ...payment_terms[paymentTerm],
        key: paymentTerm
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.paymentTerm;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="paymentTerms.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Payment Term") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="paymentTerms.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <CardWrapper>
            {payment_terms.length === 0 && !this.props.paymentTerm.loading ? (
              <Empty
                description={<IntlMessages id="paymentTerms.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.paymentTerm.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    paymentTerm: state.paymentTerm,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(PaymentTerm);
