const actions = {
  getPaymentTerms: (page, search, filter) => ({
    type: 'GET_PAYMENT_TERMS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPaymentTerm: () => ({
    type: 'CREATE_PAYMENT_TERM_REQUEST'
  }),
  storePaymentTerm: data => ({
    type: 'STORE_PAYMENT_TERM_REQUEST',
    payload: { body: data }
  }),
  editPaymentTerm: id_payment_term => ({
    type: 'EDIT_PAYMENT_TERM_REQUEST',
    payload: { id_payment_term: id_payment_term }
  }),
  updatePaymentTerm: (id_payment_term, data) => ({
    type: 'UPDATE_PAYMENT_TERM_REQUEST',
    payload: {
      id_payment_term: id_payment_term,
      body: data
    }
  }),
  showPaymentTerm: id_payment_term => ({
    type: 'SHOW_PAYMENT_TERM_REQUEST',
    payload: { id_payment_term: id_payment_term }
  }),
  deactivatePaymentTerm: id_payment_term => ({
    type: 'DEACTIVATE_PAYMENT_TERM_REQUEST',
    payload: { id_payment_term }
  }),
  activatePaymentTerm: id_payment_term => ({
    type: 'ACTIVATE_PAYMENT_TERM_REQUEST',
    payload: { id_payment_term }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
